.about-grid {
  display: grid;
  grid-template-columns: 30rem 30rem;
  gap: 1.6rem;

  &__item {
    height: 20rem;

    &--title {
      color: $color-primary;
      font-size: 3.2rem;
    }

    &--text {
      color: $color-text-grey;
      font-size: 1.6rem;
    }
  }
}
